@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Darker+Grotesque:wght@500&display=swap);
:root {
  --black-regular: #17181a;
  --blue-light: #409fff;
  --blue-regular: #0073e5;
  --gray-lighter: rgb(232, 235, 237);
  --gray-light: #d4d7d9;
  --gray-regular: #babcbf;
  --gray-dark: #6a6d70;
  --gray-darker: #484a4d;
  --white: #fff;
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  /* background-color: #17181a; */
}

.Button {
  background: var(--blue-light);
  border: 1px solid var(--blue-light);
  border-radius: 1.25em;
  color: var(--white);
  cursor: pointer;
  font-size: 1em;
  font-weight: 500;
  height: 2.5em;
  width: 9.75em;
  transition: border-color 0.2s linear, background-color 0.2s linear;
}

.Button:hover {
  background-color: var(--blue-regular);
  border-color: var(--blue-regular);
}

.Button.Secondary {
  background: none;
  color: var(--blue-light);
}

.Button.Secondary:hover {
  border-color: var(--blue-regular);
  color: var(--blue-regular);
}

.Button.small {
  width: 5.625em;
}

.Button.disabled {
  background: var(--gray-regular);
  border-color: var(--gray-regular);
  color: var(--white);
  cursor: default;
}

.Button.disabled:hover {
  background: var(--gray-regular);
  border-color: var(--gray-regular);
  color: var(--white);
  cursor: default;
}

.Card-container {
  background-color: var(--white);
  border-radius: 10px;
  box-shadow: 0 0 12px 0 rgb(0 0 0 / 10%), 0 10px 30px 0 rgb(0 0 0 / 20%);
  width: 17.5em;
}

.Card-body {
  height: 21.125em;
}

.Card-footer {
  box-sizing: border-box;
  border-top: 1px solid var(--gray-light);
  height: 4.5em;
}

.Input-container {
  border-color: var(--gray-light);
  border-style: solid;
  height: 47px;
  margin: 0;
  border-radius: 0;
  border-width: 0 0 1px;

  background: var(--white);
  color: var(--gray-dark);
  font-weight: 400;
  position: relative;
}

.Input-label {
  color: var(--gray-dark);
  font-size: 0.875em;
  position: absolute;
  top: 1.07143em;
  left: 0.8125em;
  transition: 0.2s linear;
}

.Input-label.focused {
  top: 0.4375em;
  font-size: 0.75em;
}

.Input-input {
  box-sizing: border-box;
  background: none;
  padding: 1.14286em 8% 0 0.71429em;
  border: 0;
  font-size: 0.875em;
  font-weight: 400;
  height: 100%;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.Input-input:focus {
  outline: none;
}

.Spinner-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.Spinner-svg {
  overflow: visible;
  -webkit-animation: rotate 2s linear infinite;
          animation: rotate 2s linear infinite;
}

@-webkit-keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Spinner-svg circle {
  fill: none;
  stroke: var(--gray-lighter);
}

.Spinner-svg path {
  fill: none;
  stroke: var(--blue-regular);
  stroke-linecap: round;
}

.Spinner-progress {
  margin: -2px 0 0 2px;
  position: absolute;
  color: var(--black-regular);
  font-size: 3.875em;
  font-weight: 400;
  line-height: 1;
}

.Spinner-percentage {
  color: var(--gray-regular);
  display: inline-block;
  font-size: 0.35484em;
  font-weight: 400;
  line-height: 1;
  position: relative;
  top: 0.27273em;
  vertical-align: top;
}

.Sample-container {
  position: absolute;
  left: 5em;
  top: 50%;
  margin: -12.8125em 0 0;
}

.Sample-Spinner-container {
  display: flex;
  flex-direction: column;
}

.Sample-Spinner {
  margin: 1.875em auto 0.75em;
}

.Sample-h2 {
  color: var(--black-regular);
  font-family: 'Darker Grotesque', sans-serif;
  font-size: 1.85em;
  font-weight: 400;
  line-height: 1.2;
  margin: 0 0 0.36364em;
  padding: 0 0.90909em;
  text-align: center;
}

.Sample-p {
  color: var(--gray-darker);
  font-family: 'Darker Grotesque', sans-serif;
  font-size: 1em;
  font-weight: 400;
  line-height: 1.61538em;
  margin: 0;
  padding: 0;
  text-align: center;
}

.Sample-input-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0 0.625em;
}

.Sample-buttons-container {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  padding: 4px;
}

.Sample-button-no {
  margin-right: 0.625em;
}

.App-container {
  height: 100vh;
}

