.Card-container {
  background-color: var(--white);
  border-radius: 10px;
  box-shadow: 0 0 12px 0 rgb(0 0 0 / 10%), 0 10px 30px 0 rgb(0 0 0 / 20%);
  width: 17.5em;
}

.Card-body {
  height: 21.125em;
}

.Card-footer {
  box-sizing: border-box;
  border-top: 1px solid var(--gray-light);
  height: 4.5em;
}
